html, body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  font-size: 0;
  position: relative;
}

html {
  overflow-y: scroll;
}

#controls {
  gap: 4px;
  padding: 10px;
  display: flex;
  position: absolute;
}

#controls button {
  cursor: pointer;
  color: #ccc;
  background: #efefef;
  border: 1px solid #ccc;
  border-radius: 100%;
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  box-shadow: 0 0 5px 5px #fff;
}

#controls button:hover {
  color: #999;
}

#controls span {
  color: #ccc;
  background: #fff;
  font-size: 16px;
  line-height: 20px;
  box-shadow: 0 0 5px 5px #fff;
}

#controls span span {
  box-shadow: none;
}

table {
  counter-reset: decade;
  margin: 20px auto;
}

td {
  background: #ccc;
  border-style: solid;
  border-color: #fff;
}

td.m {
  border-width: 1px 5px;
  padding: 2px 20px;
}

td.w {
  border-width: 1px;
  padding: 5px;
}

td.d {
  border-width: 0;
  padding: 1px;
}

td.none {
  background: #fff;
}

td.prev {
  background: #ccc6;
}

td.next {
  background: #ccc;
}

td.prev + td.next {
  background: green;
}

tr:before {
  content: "";
  font-size: 6px;
  line-height: 0;
  position: relative;
  left: 0;
}

tr:nth-child(10n):before {
  counter-increment: decade;
  content: counter(decade) "0";
  top: 1em;
}

